.footer{
    width: 100%;
    padding: 2rem 0;
    background-color: rgba(19,19,19,0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location {
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    margin-top: 1rem;
}

.left h1{
    font-size: 3.5rem;
    font-weight: 700;
    text-align: center;
    display: block;
    line-height: 1;
    padding-left: 2.25rem;
    padding-right: 2.25rem;
    color: white;
}

.left h4 {
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
    color: white;
}

.right h4{
    font-size: 1.2rem;
    padding-bottom: 0.1rem;
    color: white;
}

.left p{
    font-size: 1.2rem;
    color:white;
}

.highlight {
    background-color: #fff;
    color: #000;
    width: 100%;
    margin: auto;
}

.shadow-light {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, .05), 0 4px 6px -4px rgba(0, 0, 0, .05);
}

@media screen and (max-width: 640px) {
    .footer-container{
        grid-template-columns: 1fr;
    }
    
}