.pro-heading{
    text-align: center;
    padding: 2rem 0 2rem 0;
    color: #212121;
}

.pro-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.pro-card{
    background: #434343;
    padding: 1.2rem 1rem;
}

.pro-card img{
    width: 100%;
}

.pro-title{
    color: #fff;
    padding-bottom: 1rem;
}

.pro-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
    color: #ddd;
}

.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}

.pro-btns .btn{
    padding: 0.5rem 1rem;
}

@media screen and (max-width: 740px) {
    .pro-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}